.main {
  background: gainsboro;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100vh;
  width: 100%;
}
.main h1 {
  width: 100%;
  display: block;
  text-align: center;
}
.p-container {
  width: 100%;
  max-width: 300px;
  padding: 20px;
  background: white;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.p-container p {
  font-size: 20px;
  font-weight: bold;
  color: #333;
  text-align: center;
  width: 100%;
}
.p-container input {
  width: 100%;
  padding: 10px 0;
  text-align: center;
  margin: 10px 0;
  border: none;
  border-bottom: 2px solid #007bff;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.p-container input:focus {
  outline: none;
  box-shadow: 2px 2px 2px rgba(70, 60, 255, 0.5);
}
.p-container .btn {
  width: 100%;
  padding: 10px 15px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
  background: #007bff;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.p-container .btn:hover {
  background: #0056b3;
}
.main table {
  width: 100%;
  max-width: 500px;
  background: white;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.main table tr th {
  padding: 10px 5px;
  background: #f9f9f9;
  font-weight: bold;
  color: #333;
}
.main table tr td {
  padding: 10px 5px;
  text-align: center;
}
